@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@500&display=swap);

form{
    width: 300px; 
    color: white;  
    text-align: center; 

}
.wrapper{
    margin-top: 5%;
    margin-left: 5%;
    
}
.form-group{
    width: 100%;
    /* display: flex; */
    flex-direction: row;
    padding:1%
}
.form-labels{
    width: 50%;
}
.home-team-choice{
    width: 90%;
    margin-right: 5%;
}
.away-team-choice{
    width: 90%;
    margin-right: 5%;
}
.form-control{
    display: block;
    width: 5%;
    /* font-weight: 400; */
    line-height: 1.5;
    /* color: #212529; */
    /* background-color: rgb(0, 0, 0); */
    background-clip: padding-box;
    border: 1px solid #ced4da;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.form-fixture-align{
    display: flex;
    align-items: baseline;
    margin-top: 2%;
}
.form-control-home-team{
    width: 200% !important;
    text-align: center;
}
.form-control-away-team{
    width: 200% !important;
    text-align: center;
}
.form-group-button{
    display: flex;
    justify-content: center;
    margin-top: 7%;
}
.editableFormGroup{

    justify-content: space-evenly;
}
.employees-form-container{
    margin-top: 15%;
    border: solid 3px var(--business-blue);
    padding: 20px;
    width: 400px;
    border-radius: 10px;
    box-shadow: 0 0 10px lightgrey;
    display: flex;
    flex-direction: column;
    background-color: #21313C;
  }
.employees-menu-heading {
    font-weight: bold;
    font-size: 10px;
    width: 100%;
    color: var(--business-blue);
    border-bottom: 2px solid var(--business-light-grey);
    padding: 0 0 10px 0;
    margin: 0;
    text-align: center;
}
.employees-form-heading{
    font-size: 16px;
    color: white;
    font-weight: 400 ;
}
.radio-buttons-form-group{
    justify-content: space-evenly;
    display: flex;
    align-items: center;
}
